import React from "react";
import heroimage from "../assets/images/illustration-working.svg";
const heroSection = () => {
  return (
    <div className="container max-w-7xl  mx-auto p-6 pb-40 lg:flex gap-10 lg:items-center ">
      <div className="lg:w-1/2 ">
        <h1 className="lg:text-7xl text-5xl font-bold text-very_dark_violet my-5">
          More than just shorter links
        </h1>
        <p className="text-grayish_violet text-xl my-4">
          Build your brtnad's recognition and ger detailed insights on how your
          libnks are performing.
        </p>
        <button className="text-white  font-bold text-xl my-6 bg-primary1 px-14 py-4 rounded-full hover:bg-primary1hover cursor-pointer ease-in duration-150">
          Get Started
        </button>
      </div>
      <div className="lg:w-1/2  ">
        <img src={heroimage} alt="heroimage" className="" />
      </div>
    </div>
  );
};

export default heroSection;
